import React, { Component } from 'react';
import '../scss/component-specific.scss';


class ComponentOwner extends Component {

  constructor(props) {

    super(props);
  }

  render() {
    return (
      <div className="pe-loadingIndicator">
        <div className="pe-loadingIndicator-overlay"></div>
        <div className="pe-loadingIndicator-chip">
          <div className="loadingSpinnerPlaceholder"></div>
          <div className="pe-loadingIndicator-chip-text">Loading...</div>
        </div>
      </div>
    )
  }

}


export default ComponentOwner;



